  padding: 0;
}

.loader {
  width: 75px;
  height: 100px;
  z-index: 1000;
  background-image: linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0);
  background-position: 0 90px, 15px 78px, 30px 66px, 45px 58px, 60px 50px;
  background-repeat: no-repeat;
  background-size: 8px 100%;
  animation: pillerPushUp 4s linear infinite;
  display: block;
  position: fixed;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  width: 10px;
  height: 10px;
  background: #de3500;
  border-radius: 50%;
  animation: ballStepUp 4s linear infinite;
  position: absolute;
  bottom: 10px;
  left: 0;
}

@keyframes pillerPushUp {
  0%, 40%, 100% {
    background-position: 0 90px, 15px 78px, 30px 66px, 45px 58px, 60px 50px;
  }

  50%, 90% {
    background-position: 0 50px, 15px 58px, 30px 66px, 45px 78px, 60px 90px;
  }
}

@keyframes ballStepUp {
  0% {
    transform: translate(0);
  }

  5% {
    transform: translate(8px, -14px);
  }

  10% {
    transform: translate(15px, -10px);
  }

  17% {
    transform: translate(23px, -24px);
  }

  20% {
    transform: translate(30px, -20px);
  }

  27% {
    transform: translate(38px, -34px);
  }

  30% {
    transform: translate(45px, -30px);
  }

  37% {
    transform: translate(53px, -44px);
  }

  40% {
    transform: translate(60px, -40px);
  }

  50% {
    transform: translate(60px);
  }

  57% {
    transform: translate(53px, -14px);
  }

  60% {
    transform: translate(45px, -10px);
  }

  67% {
    transform: translate(37px, -24px);
  }

  70% {
    transform: translate(30px, -20px);
  }

  77% {
    transform: translate(22px, -34px);
  }

  80% {
    transform: translate(15px, -30px);
  }

  87% {
    transform: translate(7px, -44px);
  }

  90% {
    transform: translate(0, -40px);
  }

  100% {
    transform: translate(0);
  }
}

